input {
  width: 100%;
  height: 3em;
  margin: 1em 0px;
  box-sizing: border-box;
  padding: 0.5em;
  box-shadow: 0px 0px 3px 1px #dcdcdc;
  border: none;
  border-radius: 5px;
}

.input-container {
  width: 100%;
  font-size: 1.2em;
  font-family: 'Ubuntu', sans-serif;
  color: #2b2c34;
}
